body {
	background-color: white;
	font-family: 'Open Sans', sans-serif;
	font-weight: 300;
	line-height: 1.2;
}

body a {
	color: black;
	font-weight: 600;
}

nav {
	padding: 2%;
	color: black;
}

li {
	padding-left: 2em;
	padding-bottom: 2em;
}

footer {
	background-color: black;
	color: #F2F8FF;
	padding: 3% 20% 2% 20%;
}

.nav-link {
	padding: 1%;
	font-size: 1.5rem;
}

.section {
	margin: 7% 20% 7% 20%;
}

.section-0 {
	margin: 5% 20% 0% 20%;
}

.section h1 {
	padding-bottom: 2%;
	color: black;
	font-size: 2.5rem;
}

.bg {
	padding: 0% 0% 0% 0%;
}

.bg-white {
	background-color: white;
	color: #333745;
}

.bg-color {
	background-color: white;
	color: black;
}

.bg-color-2 {
/*	background-color: #FE5F55;
	color: white;*/
	background-color: white;
	color:  #333745;
	font-weight: 600;
}

.bg-color-2 a {
	color: black;
}

.bg-color a {
	color: black;
}
.bg-color-3 {

	color: black;
}

.title h1 {
	font-size: 11rem;
	font-weight: 400;
	margin-top: 10%;
	color: #ea0e55;
}

.quote {
	font-style: italic;
}